import React from 'react';
import {
  Box,
  Card,
  CardContent,
  Chip,
  Grid,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Typography,
} from '@mui/material';
import { Done } from '@mui/icons-material';
import SectionGrid from './SectionGrid';

interface PricingEntity {
  lvl: string
  name: string
  price: number
  full?: boolean
  recommended?: boolean
}

const Pricing = React.forwardRef((_, ref) => {
  const pricings = React.useMemo<Array<PricingEntity>>(
    () => [
      {
        lvl: 'pocztkujcy',
        name: '4 wejcia',
        price: 80,
      },
      {
        lvl: 'niezaawansowany',
        name: '8 wej',
        price: 110,
      },
      {
        lvl: 'redniozaawansowany',
        name: '12 wej',
        price: 130,
        recommended: true,
      },
      {
        lvl: 'zaawansowany',
        name: '16 wej',
        price: 150,
      },
      {
        lvl: 'stali bywalcy',
        name: 'open',
        price: 210,
        full: true,
      },
    ],
    [],
  );

  const info = React.useMemo<Array<string>>(
    () => [
      'Opata wpisowa roczna na ubezpieczenie i zwizki sportowe 100PLN.',
      'Pierwszy trening gratis.',
      'Wejcia wane jeden miesic.',
    ],
    [],
  );

  return (
    <Box
      className="irs-cennik"
      ref={ref as any}
    >
      <SectionGrid>
        <Typography variant="h2">Cennik wej</Typography>
        <Grid
          container
          spacing={2}
        >
          {pricings.map((offer) => (
            <Grid
              key={offer.name}
              item
              md={offer.full ? 12 : 6}
              xs={12}
            >
              <Card
                sx={{
                  position: 'relative',
                  marginX: offer.recommended ? 0 : 2,
                  paddingY: {
                    md: 6,
                    xs: 1,
                  },
                  paddingX: {
                    md: offer.recommended ? 8 : 6,
                    xs: offer.recommended ? 3 : 1,
                  },
                }}
                elevation={offer.recommended ? 6 : 1}
              >
                <CardContent
                  sx={{
                    textAlign: 'center',
                  }}
                >
                  <Typography variant="h5">
                    {offer.lvl}
                  </Typography>
                  <Typography
                    variant="subtitle1"
                    sx={{
                      marginY: {
                        md: 2,
                      },
                    }}
                  >
                    {offer.name}
                  </Typography>
                  <Chip
                    label={`${offer.price}PLN`}
                    color={offer.recommended ? 'success' : 'default'}
                  />
                  {offer.recommended && (
                    <Chip
                      sx={{
                        position: 'absolute',
                        bottom: 22,
                        left: -53,
                        transform: 'rotate(33deg)',
                        boxShadow: 6,
                        paddingX: 5,
                      }}
                      label="najczstrzy wybr"
                      color="info"
                    />
                  )}
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
        <List>
          {info.map((txt) => (
            <ListItem key={txt}>
              <ListItemIcon>
                <Done />
              </ListItemIcon>
              <ListItemText>{txt}</ListItemText>
            </ListItem>
          ))}
        </List>
      </SectionGrid>
    </Box>
  );
});

export default Pricing;
